
.todosAdded{
    width: 40%;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    position: relative;
}

.todo{
    width: 100%;
    background-color: #b2a9a9;
    height: 55px;
    display: flex;
    align-items: center;
    padding: 15px 0px 15px 35px;
    margin: 0;
    font-size: 19px;
    font-family: monospace;
    border-radius: 5px;
}

.todoIcon{
    position: absolute;
    cursor: pointer;
}

.tick{
    right: 70px;
    height: 35px;
    width: 35px;
}

.trash{
    right: 30px;
    height: 24px;
    width: 22px;
}

.complete{
    opacity: 0.5;
    text-decoration: line-through;
}