.parent-app{
    width: 90%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-bottom: 100px;
    margin-top: 30px;
}

.parent-app h3{
    width: 100%;
    margin: 30px 0px 30px 0px;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: bold;
    font-family: sans-serif;
}

.input-container{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 100px;
}

.inputIcont{
    width: 20%;
    display: flex;
    position: relative;
    margin-right: 8px;
}

.todoInput{
    height: 35px;
    width: 100%;
    background-color: #dbdcdd7d;
    padding: 0;
    border: 2px solid black;
    border-radius: 3px;
    border-right: 0px;
}

.todoInput::placeholder{
    padding-left: 10px;
}

.plusIcon{
    height: 35px;
    width: 38px;
    position: absolute;
    right: -4px;
    cursor: pointer;
}

.select{
    height: 33px;
    width: 10%;
    font-size: 15px;
    font-weight: bold;
    border-radius: 3px;
    font-family: system-ui;
    border-radius: 3px;
    background-color: #dbdcdd7d;
    padding: 0;
    border: 2px solid black;
    cursor: pointer;
}